"use client";

import React, { useEffect } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { ArrowLeft, FileText, Loader2 } from 'lucide-react';
import { useUnlockStore, type Subject } from '@/hooks/use-unlock-store';
import { Button } from '@/components/ui/button';
import { useToast } from '@/hooks/use-toast';
import { Card, CardContent } from './ui/card';

export default function PDFViewer({ subjectName }: { subjectName: Subject }) {
    const router = useRouter();
    const { toast } = useToast();
    const { isUnlocked, isInitialized } = useUnlockStore();

    useEffect(() => {
        if (isInitialized && !isUnlocked(subjectName)) {
            toast({
                title: 'Access Denied',
                description: `You must unlock ${subjectName} to view this content.`,
                variant: 'destructive',
            });
            router.push('/');
        }
    }, [isInitialized, isUnlocked, subjectName, router, toast]);

    if (!isInitialized || !isUnlocked(subjectName)) {
        return (
            <div className="flex h-screen w-full flex-col items-center justify-center bg-background text-foreground">
                <Loader2 className="h-12 w-12 animate-spin text-primary" />
                <p className="mt-4 text-lg">Verifying access...</p>
            </div>
        );
    }

    return (
        <div 
            className="flex flex-col h-screen bg-background text-foreground"
        >
            <header className="flex items-center justify-between p-4 border-b shrink-0">
                <Button asChild variant="outline">
                    <Link href="/">
                        <ArrowLeft className="mr-2 h-4 w-4" />
                        Back to Subjects
                    </Link>
                </Button>
                <h1 className="text-xl font-bold font-headline capitalize text-center">
                    {subjectName} Study Material
                </h1>
                <div className="w-40 text-right">
                    <span className="text-sm text-muted-foreground">Status: Unlocked</span>
                </div>
            </header>
            
            <main className="flex-grow p-4 md:p-6 overflow-hidden">
                <Card className="h-full w-full shadow-inner">
                    <CardContent className="h-full w-full p-4 md:p-6 flex items-center justify-center">
                        <div className="text-center text-muted-foreground">
                            <FileText className="h-16 w-16 mx-auto mb-4" />
                            <h2 className="text-2xl font-headline text-foreground">Content Coming Soon</h2>
                            <p>The study materials for this subject are being prepared.</p>
                        </div>
                    </CardContent>
                </Card>
            </main>
            
            <footer className="text-center text-xs text-muted-foreground p-3 border-t shrink-0">
                <p>Content is protected. Downloading and distribution are prohibited.</p>
            </footer>
        </div>
    );
}
